import { getLeavesClient } from '@mintlify/http-client';
import { LlmsTxtResponseType } from '@mintlify/http-client/dist/leaves-client/types';
import type { ServerResponse } from 'http';

function LlmsTxt() {}

function generateLlmsTxt(response: LlmsTxtResponseType): string {
  let llmsTxt = `# ${response.name}

## Docs

${response.pages.reduce((acc, page) => `${acc}- [${page.title}](${response.domain}/${page.path}.md)${page.description ? `: ${page.description}` : ''}\n`, '')}`;

  if (response.links.length > 0) {
    llmsTxt += `\n\n## Optional

${response.links.reduce((acc, link) => `${acc}- [${link.name}](${link.url})\n`, '')}`;
  }

  return llmsTxt;
}

export async function getServerSideProps({
  params: { subdomain },
  res,
}: {
  params: { subdomain: string };
  res: ServerResponse;
}) {
  try {
    const client = getLeavesClient(subdomain);
    const response = await client.getLlmsTxt();

    const llmsTxt = generateLlmsTxt(response);

    res.setHeader('Content-Type', 'text/plain; charset=utf-8');
    res.write(llmsTxt);
    res.end();

    return {
      props: {},
    };
  } catch (error) {
    console.error('Error fetching llms.txt:', error);
    return {
      notFound: true,
    };
  }
}

export default LlmsTxt;
